<template>
    <div class="row">
        <!-- Index -->
        <div class="col-2" @dragover.prevent @drop="taskReset">
            <h6><fa-icon icon="tasks"  /> Task Stack</h6>
            <ul class="tasks list-unstyled mb-0">
                <li v-for="(task, idx) in tasks" :key="idx">
                    <div draggable="true" @dragstart="onDragStart(task.id, $event)" @dragend="onDragEnd(idx, $event)">
                        <task v-if="!task.isDone && !task.isArchived && !task.isScheduled" :task="task" @days-change="onDaysChange(task.id, $event)"  />
                    </div>
                </li>
            </ul>
            <div>{{ tasksFinishedRate }} <fa-icon icon="percentage" /></div>
        </div>
        <div class="col-8 gantt" ref="gantt" @dragover.prevent @drop="onDrop">
            <span class="d-1"></span>
            <span class="d-2"></span>
            <span class="d-3"></span>
            <span class="d-4"></span>
            <span class="d-5"></span>
            <span class="d-6"></span>
            <span class="d-7"></span>
            <span class="d-8"></span>
            <span class="d-9"></span>
            <span class="d-10"></span>

            <h6 class="days h-mon0">Mon</h6>
            <h6 class="days h-tue0">Tue</h6>
            <h6 class="days h-wed0">Wed</h6>
            <h6 class="days h-thu0">Thu</h6>
            <h6 class="days h-fri0">Fri</h6>
            <h6 class="days h-mon1">Mon</h6>
            <h6 class="days h-tue1">Tue</h6>
            <h6 class="days h-wed1">Wed</h6>
            <h6 class="days h-thu1">Thu</h6>
            <h6 class="days h-fri1">Fri</h6>

            <fragment v-for="(task, idx) in tasks">
                <div v-if="!task.isArchived && task.isScheduled" :style="`grid-column: ${task.column} / span ${task.days*2}; grid-row: ${idx+2};`" :key="idx" draggable="true"  @dragstart="onDragStart(task.id, $event)" @dragend="onDragEnd(idx, $event)">
                    <task :task="task" :expand="true" @days-change="onDaysChange(task.id, $event)" @title-change="onTitleChange(task.id, $event)" @toggle-done="onToggleDone(task.id, $event)" @toggle-archive="onToggleArchive(task.id, $event)" />
                    <i class="task-indicator" :class="{'badge-danger': !task.isDone, 'badge-secondary': task.isDone}"></i>
                </div>
            </fragment>
        </div>
        <div class="col-2">
            <h6><b>Archived Tasks</b></h6>
            <ul class="list-unstyled">
                <li v-for="(task, idx) in tasks.filter(v => v.isArchived)" :key="idx">
                    <small>
                        <span class="badge" :class="{'badge-info':task.isDone, 'badge-danger':!task.isDone}">{{ task.from }}</span> {{ task.task }}<br />
                        <i>{{ toDatetime(task.created) }}</i>
                    </small>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex';
    import moment from 'moment'
    import Task from '@/components/Task';
    import {TASK_SET_DAYS, TASK_SET_TITLE, TASK_TOGGLE_DONE, TASK_TOGGLE_ARCHIVE} from '../mutation-types';

    export default {
        name: "Tasks",

        computed: {
            ...mapState(['tasks']),

            tasksFinishedRate() {
                const total = this.tasks.length;
                if (total === 0) return 0;
                const n = this.tasks.reduce((i, v) => {
                    return v.isDone ? ++i : i;
                }, 0);
                return (n / total * 100).toFixed(0);
            },

            spanWidth() {
                return this.$refs.gantt.clientWidth / 20;
            },

            toDatetime: function() {
                return function(unixtime) {
                    return moment(parseInt(unixtime)).format('YYYY-MM-DD HH:mm');
                }
            }

        },

        data() {
            return {
            }
        },

        methods: {
            calSpan(evt) {
                return Math.floor((evt.pageX - this.$refs.gantt.offsetLeft + this.$refs.gantt.clientLeft) / this.spanWidth) + 1;
            },

            onDaysChange(id, days) {
                this.$store.commit(TASK_SET_DAYS, {id, days});
            },

            async onTitleChange(id, task) {
                await this.$db.tasks.update(id, {task});
                this.$store.commit(TASK_SET_TITLE, {id, task});
            },

            async onToggleDone(id, done) {
                await this.$db.tasks.update(id, {isDone: done});
                this.$store.commit(TASK_TOGGLE_DONE, {id, isDone: done});
            },

            async onToggleArchive(id, archive) {
                await this.$db.tasks.update(id, {isArchived: archive});
                this.$store.commit(TASK_TOGGLE_ARCHIVE, {id, isArchived: archive});
            },

            onDragStart(id, evt) {
                evt.dataTransfer.setData('task', id);
            },

            onDragEnd(idx, evt) {
                evt.dataTransfer.clearData();
            },

            onDrop(evt) {
                const id = parseInt(evt.dataTransfer.getData('task'));
                this.$store.dispatch('taskToggleScheduled', {db: this.$db, id, column: this.calSpan(evt), v: true});
            },

            taskReset(evt) {
                const id = parseInt(evt.dataTransfer.getData('task'));
                this.$store.dispatch('taskReset', {db: this.$db, id});
            }

        },

        components: {
            Task
        }
    }
</script>

<style scoped>
    .tasks li:not(last-child) {
        margin-bottom: .2rem;
    }

    .task-indicator {
        display: inline-block;
        height: .7rem;
        width: 100%;
        border-radius: .5rem;
        border: thin solid grey;
    }

    .gantt {
        display: grid;
        grid-template-columns: repeat(20, 1fr);
        grid-gap: 0 0;
        width: 100%;
    }

    .gantt span {
        border-left: 2px dashed lightgrey;
        grid-row: 1 / span 20;
    }

    .gantt span:nth-child(5) {
        border-left: 2px solid grey;
    }

    span.d-1 { grid-column: 3; }
    span.d-2 { grid-column: 5; }
    span.d-3 { grid-column: 7; }
    span.d-4 { grid-column: 9; }
    span.d-5 { grid-column: 11; }
    span.d-6 { grid-column: 13; }
    span.d-7 { grid-column: 15; }
    span.d-8 { grid-column: 17; }
    span.d-9 { grid-column: 19; }
    span.d-10 { grid-column: 21; }

    .gantt h6.days {
        font-weight: bold;
        text-align: center;
        grid-row: 1;
    }

    h6.h-mon0 { grid-column: 1 / span 2; }
    h6.h-tue0 { grid-column: 3 / span 2; }
    h6.h-wed0 { grid-column: 5 / span 2; }
    h6.h-thu0 { grid-column: 7 / span 2; }
    h6.h-fri0 { grid-column: 9 / span 2; }
    h6.h-mon1 { grid-column: 11 / span 2; }
    h6.h-tue1 { grid-column: 13 / span 2; }
    h6.h-wed1 { grid-column: 15 / span 2; }
    h6.h-thu1 { grid-column: 17 / span 2; }
    h6.h-fri1 { grid-column: 19 / span 2; }

    .gantt h6.half-days {
        color: grey;
        font-weight: normal;
        font-style: italic;
        text-align: center;
        grid-row: 2;
    }
</style>
