<template>
    <div class="task">
        <div class="task-header">
            <span class="task-label" :class="{'badge-danger': !task.isDone, 'badge-secondary': task.isDone}" :title="task.from==='static' ? 'definition' : (task.from==='dynamic' ? 'functional' : task.from)">
                <fa-icon :icon="task.from==='static' ? 'suitcase' : (task.from==='dynamic' ? 'fan' : 'store-alt')" />
            </span>
            <input v-if="expand" type="text" :value="task.task" @input="$emit('title-change', $event.target.value)" />
            <span v-else>{{ task.task }}</span>
            <label v-if="expand" class="task-days-label mb-0">
                <input class="task-days" type="number" step=".5" min="1" max="10" :value="task.days" @change="$emit('days-change', $event.target.value)" />
                <!--                <span>days</span>-->
            </label>
            <label v-if="expand" class="ml-2 mb-0">
                <input type="checkbox" style="vertical-align: text-bottom" @change="$emit('toggle-done', $event.target.checked)" /> Done
            </label>
            <label v-if="expand" class="ml-1 mb-0">
                <input type="checkbox" style="vertical-align: text-bottom" @change="$emit('toggle-archive', $event.target.checked)" /> Archive
            </label>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Task",
        props: {
            expand: {type: Boolean, default: false},
            task: {type: Object}
        }
    }
</script>

